<template>
	<div id="product-ranking">
		<!--顶部菜单-->
		<nav-bar class="home-nav-bar">
			<div slot="left" class="icon-left" @click="backClick">
				<img src="~assets/images/common/prev2.svg">
			</div>
			<div slot="center">
				销售货品排行
			</div>
			<div slot="right">
				图标
			</div>
		</nav-bar>
		<!--顶部菜单客户页面也用到所以抽出来-->
		<ranking-top-navs/>
		<!--数量和金额客户页面也用到所以抽出来-->
		<ranking-filter-navs :test="fiterArr" @itemClcik="rankingClcik"/>
		<!--滚动--->
		<scroll class="product-ranking-scroller"
				ref="scroller"
				:probe-type=3
				@pullingup="loadMove"
		>
		<!--排行列表-->
		<ranking-content :productArr="cproductkingArr"/>
		</scroll>
	</div>
</template>

<script>

import NavBar from 'components/common/navbar/NavBar'
import Scroll from 'components/common/scroll/Scroll'
import RankingTopNavs from 'components/contents/rankingnavs/RankingTopNavs'
import RankingFilterNavs from 'components/contents/rankingnavs/RankingFilterNavs'
import RankingContent from './childComps/RankingContent'

import {getGoodsDatas} from 'network/product'


export default {
  name: 'ProductRanking',
	components: {
		NavBar,
		Scroll,
		RankingTopNavs,
		RankingFilterNavs,
		RankingContent
	},
	data(){
		return{
			fiterArr:["","","数量","金额"],
			productkingObj:{
				list:[],
				page:0,
				isMore:true,
			},
			sortType:'sales',				//当前选中排序类型
		}
	},
	methods:{
		//返回按钮
		backClick(){
			//this.$router.replace("/customersdetail").catch(res=>{res})
			this.$router.go(-1)
		},
		//加载更多
		loadMove(){
			//防抖
			if(timer) clearTimeout(timer)
			let timer=setTimeout(()=>{
				this.getGoodsDatas('allSelect',"",1)
				if(this.productkingObj.isMore){
					this.$refs.scroller.finishPullUp()
				}
					this.$refs.scroller.refresh()
			},50)
		},
		//数量和金额点击
		rankingClcik(index){
			switch(index){
				case 2:
					this.sortType="sales"
					break
				case 3:
					this.sortType="price"
					break
			}
		},
		//排序-需要抽到工具类
		compare (property){
			return function(a,b){
				let value1=a[property]
				let value2=b[property]
				return value2-value1
			}
		},
		//获取商品数据
		getGoodsDatas(type,productnum,typeid){
			let page=this.productkingObj.page+1;
			getGoodsDatas(type,page,productnum,typeid).then(res=>{
				console.log(res)
				this.productkingObj.list.push(...res);
				this.productkingObj.page+=1			//如果没有数据了
				if(res.length<=0){
					//不在加载更多
					this.productkingObj.isMore=false
				}
			})
		}
	},
	mounted(){
		
	},
	computed:{
		cproductkingArr(){
			//排序
			let arr=this.productkingObj.list
			return arr.sort(this.compare(this.sortType))
		}
	},
	created(){
		this.getGoodsDatas('allSelect',"",1)
	}
}
</script >

<style spoced>
  .icon-left{
	text-align:left;
	position:relative;
  }
  .icon-left img{
	position:absolute;
	top:10px;
	left:10px;
  }
#product-ranking{
	background:#f3f3f3;
    height: 100vh;
    position: relative;
	z-index:12;
}

.product-ranking-scroller{
    /*height:300px;*/
    position: relative;
	height:calc(100% - 101px - 49px);
    right: 0;
    left: 0;
	overflow:hidden;
  }
</style>
